import React from "react";
import { Link } from "react-router-dom";
// import { useState } from "react";
// import AuthModal from "./AuthModal";



const Navbar = () => {
  // const [showModal, setShowModal] = useState(false);
  const authToken = true;
  // const handleClick = () => {
  //   console.log('clciked')
  //   setShowModal(true)
  // }

  return (
    <>
      <header className="text-gray-600 body-font">
        <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
          <Link
            to="/breedit"
            className="flex title-font font-medium items-center text-gray-900 mb-4 md:mb-0"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              stroke="currentColor"
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              className="w-10 h-10 text-white p-2 bg-indigo-500 rounded-full"
              viewBox="0 0 24 24"
            >
              <path d="M12 2L2 7l10 5 10-5-10-5zM2 17l10 5 10-5M2 12l10 5 10-5"></path>
            </svg>
            <span className="ml-3 text-xl">BREEDIT</span>
          </Link>
          <nav className="md:ml-auto md:mr-auto flex flex-wrap items-center text-base justify-center">
            <Link to="/breedit" className="mr-5 hover:text-gray-900">
              Home
            </Link>
            <Link to="/findPet" className="mr-5 hover:text-gray-900">
              Find a pet
            </Link>
            <Link to="/chat" className="mr-5 hover:text-gray-900">
              Chat
            </Link>
            <Link to="/onBoarding" className="mr-5 hover:text-gray-900">
              Profile
            </Link>
          </nav>
          <Link to="/login">
            <button className="inline-flex items-center bg-gray-100 border-0 py-1 px-3 focus:outline-none hover:bg-gray-200 rounded text-base mt-4 md:mt-0">
              {authToken ? "Signout" : "Create Account"}
              <svg
                fill="none"
                stroke="currentColor"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                className="w-4 h-4 ml-1"
                viewBox="0 0 24 24"
              >
                <path d="M5 12h14M12 5l7 7-7 7"></path>
              </svg>
            </button>
          </Link>
          {/* {
            showModal && (
              <AuthModal setShowModal={setShowModal}/>
            )
          } */}
        </div>
      </header>
    </>
  );
};

export default Navbar;
